.portfolio-inner {
  padding: 30px 48px 94px 67px;

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-top: -5px;
    margin-left: -5px;
    padding-top: 24px;
  }

  &__item {
    flex-basis: 33.3%;
    padding-top: 5px;
    padding-left: 5px;
    box-sizing: border-box;
  }

  &__item:first-child {
    flex-basis: 100%;
  }

  &__link {
    display: block;
    height: 100%;
    box-sizing: border-box;
    padding: 1px;
    transition: transform 0.3s;

    &:hover,
    &:focus {
      transform: scale(1.01);
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }

  .modal {
    &__content {
      padding: 50px 50px 15px;
      max-width: 80vw;
      max-height: 80vh;
      overflow: hidden;
    }

    .swiper-container {

    }

    &__slider-item {
      height: initial;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      display: block;
    }
  }


  @media screen and (max-width: 1024px) {
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (max-width: 768px) {
    &__item:first-child,
    &__item {
      flex-basis: 50%;
    }
  }

  @media screen and (max-width: 540px) {
    &__item:first-child,
    &__item {
      flex-basis: 100%;
    }
  }
}
